import "./styles.css";
import { useEffect, useMemo, useState } from "react";
import Start from "./Component/Start";
import Timer from "./Component/Timer";
import Quiz from "./Component/Quiz";

function App() {
  const [username, setUsername] = useState(null);
  const [timeOut, setTimeOut] = useState(false);
  const [questionNumber, setQuestionNumber] = useState(1);
  const [earned, setEarned] = useState("₹ 0");

  const data = [
    {
      id: 1,
      question: "when newton school started?",
      answers: [
        {
          text: "2018",
          correct: false
        },
        {
          text: "2019",
          correct: true
        },
        {
          text: "2020",
          correct: false
        },
        {
          text: "2021",
          correct: false
        }
      ]
    },
    {
      id: 2,
      question: "who is the founder of newton school?",
      answers: [
        {
          text: "Siddharth Maheshwari",
          correct: true
        },
        {
          text: "Indrajeet",
          correct: false
        },
        {
          text: "Apoorv",
          correct: false
        },
        {
          text: "Nishant Chandra",
          correct: false
        }
      ]
    },
    {
      id: 3,
      question: "Which is better to get job ?",
      answers: [
        {
          text: "AccioJob",
          correct: false
        },
        {
          text: "Great Learning",
          correct: false
        },
        {
          text: "Masai",
          correct: false
        },
        {
          text: "Newton School",
          correct: true
        }
      ]
    }
  ];

  const moneyPyramid = useMemo(
    () =>
      [
        { id: 1, amount: "₹ 100" },
        { id: 2, amount: "₹ 200" },
        { id: 3, amount: "₹ 300" },
        { id: 4, amount: "₹ 500" },
        { id: 5, amount: "₹ 1.000" },
        { id: 6, amount: "₹ 2.000" },
        { id: 7, amount: "₹ 4.000" },
        { id: 8, amount: "₹ 8.000" },
        { id: 9, amount: "₹ 16.000" },
        { id: 10, amount: "₹ 32.000" },
        { id: 11, amount: "₹ 64.000" },
        { id: 12, amount: "₹ 125.000" },
        { id: 13, amount: "₹ 250.000" },
        { id: 14, amount: "₹ 500.000" },
        { id: 15, amount: "₹ 1.000.000" }
      ].reverse(),
    []
  );

  useEffect(() => {
    questionNumber > 1 &&
      setEarned(moneyPyramid.find((m) => m.id === questionNumber - 1).amount);
  }, [questionNumber, moneyPyramid]);

  return (
    <div className="app">
      {!username ? (
        <Start setUsername={setUsername} />
      ) : (
        <>
          <div className="main">
            {timeOut ? (
              <h1 className="endText">You earned: {earned}</h1>
            ) : (
              <>
                <div className="top">
                  <div className="timer">
                    <Timer
                      setTimeOut={setTimeOut}
                      questionNumber={questionNumber}
                    />
                  </div>
                </div>
                <div className="bottom">
                  <Quiz
                    data={data}
                    questionNumber={questionNumber}
                    setQuestionNumber={setQuestionNumber}
                    setTimeOut={setTimeOut}
                  />
                </div>
              </>
            )}
          </div>
          <div className="pyramid">
            <ul className="moneylist">
              {moneyPyramid.map((m) => (
                <li
                  className={
                    questionNumber === m.id
                      ? "moneylistitem active"
                      : "moneylistitem"
                  }
                >
                  <span className="moneylistitemnumber">{m.id}</span>
                  <span className="moneylistitemamount">{m.amount}</span>
                </li>
              ))}
            </ul>
          </div>
        </>
      )}
    </div>
  );
}

export default App;
